@import './mixins.scss';

.edit-item-container label:has(+ input[type="text"]:focus:read-write)::after,
.edit-item-container label:has(+ textarea:focus:read-write)::after {
    content: '';
    background-image: url(../icons/edit-button-svg.svg);
    background-size: contain; /* or specify width and height */

    width: 17px; /* Desired width */
    height: 17px; /* Desired height */
    position: absolute;
    transform: translate(3px, 0.5px);

    filter: invert(100%) brightness(1000%);
}

// disabled for checklist items 
.list-items-fieldset label:has(+ input[type="text"]:focus:read-write)::after,
.list-items-fieldset label:has(+ textarea:focus:read-write)::after {
    display: none;
}
